$color : rgb(242, 151, 27);

.contact {
  form {
    padding: 10px 0;

    p {
      margin: 0;
      font-size: 12px;
    }
    label {
      display: block;
      padding: 5px 0;
    }
    input {
      &:focus {
        box-shadow: 0px 0px 5px 2px $color;		/*影を付ける*/
        outline-style:none;;
      }
    }
    textarea {
      &:focus {
        box-shadow: 0px 0px 5px 2px $color;		/*影を付ける*/
	      outline-style:none;
      }
    }

    .form_submit {
      border: none;
      color: #fff;
      background-color: $color;
      border-radius: 4px;
      width: 80px;
      font-size: 14px;
      transition-duration: 0.3s;

      &:hover {
        cursor: pointer;
        opacity: 0.8;
      }
    }

  }

  h3 {
    font-size: 12px;
    opacity: 0.5;
  }
}